import citydata from './citydata'
export default {
  data() {
    return {
      // 物流数据
      expressInfo: [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: '已签收,感谢使用顺丰,期待再次为您服务',
          location: ''
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context:
            '[北京市]北京海淀育新小区营业点派件员 顺丰速运 95338正在为您派件',
          location: ''
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: '快件到达 [北京海淀育新小区营业点]',
          location: ''
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context:
            '快件在[北京顺义集散中心]已装车,准备发往 [北京海淀育新小区营业点]',
          location: ''
        },
        {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: '快件到达 [北京顺义集散中心]',
          location: ''
        },
        {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '快件在[北京宝胜营业点]已装车,准备发往 [北京顺义集散中心]',
          location: ''
        },
        {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: '顺丰速运 已收取快件',
          location: ''
        },
        {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '卖家发货',
          location: ''
        },
        {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: '您的订单将由HLA（北京海淀区清河中街店）门店安排发货。',
          location: ''
        },
        {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '商品已经下单',
          location: ''
        }
      ],
      // 查询条件
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 总条数
      total: 0,
      // 获取到的订单列表
      ordersList: [],
      // 城市信息
      citydata,
      // 地址对话框可视度
      adressDialogVisible: false,
      // 地址表单
      adressForm: {
        adress1: [],
        adress2: ''
      },
      // 配置级联选择器
      props: {
        label: 'label',
        value: 'value',
        children: 'children',
        expandTrigger: 'hover'
      },
      // 地址表单验证规则
      adressRules: {
        adress2: [
          { required: true, message: '请输入详细地址', trigger: 'blur' }
        ]
      },
      // 配送信息对话框可视度
      expressDialogVisible: false
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    // 获取订单列表
    async getOrdersList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message('获取数据失败')
      }
      this.total = res.total
      this.ordersList = res.data.goods
    },
    // 监听pagesize变化事件
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize
      this.getOrdersList()
    },
    // 监听pagenum变化事件
    handleCurrentChange(newnum) {
      this.queryInfo.pagenum = newnum
      this.getOrdersList()
    },
    // 展示地址对话框
    showAdress() {
      this.adressDialogVisible = true
    },
    // 监听级联选择器更改事件
    handleChange() {
      this.$message.info('当前城市已发生改变')
    },
    // 对话框关闭 重置表单
    resetADialog() {
      this.$refs.adressRef.resetFields()
      this.adressForm.adress1 = []
    },
    // 确认更改地址
    confirmAdress() {
      this.$refs.adressRef.validate((valid) => {
        if (!valid || this.adressForm.adress1.length === 0) {
          return this.$message.error('请完善信息')
        }
        this.$message.success('更改成功')
        this.adressDialogVisible = false
      })
    },
    // 展示配送对话框
    async showExpress() {
      console.log(this.expressInfo)
      this.expressDialogVisible = true
    }
  }
}
